window.addEventListener('DOMContentLoaded', function () {

    // Tabs
    let tabs = document.querySelectorAll('.tabheader__item'), //  
        tabsContent = document.querySelectorAll('.tabcontent'), // , 
        tabsParent = document.querySelector('.tabheader__items'); // 

    function hideFunc() {   //   
        tabsContent.forEach(item => { //   
            item.style.display = 'none'; //    
        });
        tabs.forEach(item => { //    
            item.classList.remove('tabheader__item_active'); //    -   ,   .
        });
    }

    function showFunc(i = 0) {  // .   ,    0.  , 1 .
        tabsContent[i].style.display = 'block'; //      = 0,   .. display=block
        tabs[i].classList.add('tabheader__item_active'); //       

    }

    hideFunc(); //  
    showFunc(); //   ,  ..   ,    .

    tabsParent.addEventListener('click', (event) => { //  .     ,        .
        let target = event.target; //  .
        if (target && target.classList.contains('tabheader__item')) { //          tabheader__item
            tabs.forEach((item, i) => { //       .
                if (target == item) { //     , 
                    hideFunc(); //  (   -   )
                    showFunc(i); //    ,   .
                }
            });
        }
    });



    // Timer

    const deadline = '2022-10-27'; //   



    function getTimeRemaining(endtime) { //   

        let days, hours, minutes, seconds; //        
        const t = Date.parse(endtime) - Date.parse(new Date()); //       Date.parse        .            .       .

        if (t <= 0) {
            days = 0;
            hours = 0;
            minutes = 0;
            seconds = 0;
        } else {
            days = Math.floor((t / (1000 * 60 * 60 * 24))); //   .      math.floor.     (     60(      )     60(    )      24 (      ) ).    -     .                   .
            hours = Math.floor((t / (1000 * 60 * 60) % 24)); // (         )   %  24  %     . (%: 5 % 2 = 1.  5/2=4  1  )
            minutes = Math.floor((t / 1000 / 60) % 60); // (   1000        ,    60    ) % 60 ..     .     . (:       60).
            seconds = Math.floor((t / 1000) % 60); // (   100      )  %   60. 
        }

        return { //          .
            'total': t, // 
            'days': days, // 
            'hours': hours, //
            'minutes': minutes, //
            'seconds': seconds //
        };
    }

    function getZero(num) { //      10.   2  02.  6  06. 10     .. .   - .
        if (num >= 0 && num < 10) { //          .
            return `0${num}`; //          .
        } else { //    10
            return num; //        ..  .
        }
    }


    function setClock(selector, endtime) { //     .  2 .

        //  :
        const timer = document.querySelector(selector), //   -    .      ,       .
            days = timer.querySelector('#days'), //   #days        
            hours = timer.querySelector('#hours'), //   #hours        
            minutes = timer.querySelector('#minutes'), //   #minutes        
            seconds = timer.querySelector('#seconds'), //   #seconds        
            timeInterval = setInterval(updateClock, 1000); // ,         updateClock.   . - :)

        updateClock(); //  ,  ,             .

        function updateClock() { //    .     .
            const t = getTimeRemaining(endtime); //      "   "  (       ).   t       .

            //      
            days.innerHTML = getZero(t.days);  //    (days = timer.querySelector('#days')) #days    , ,      .
            hours.innerHTML = getZero(t.hours);//    (hours = timer.querySelector('#hours')) #hours    , ,      .
            minutes.innerHTML = getZero(t.minutes);//    (minutes = timer.querySelector('#minutes')) #minutes    , ,      .
            seconds.innerHTML = getZero(t.seconds);//    (seconds = timer.querySelector('#seconds')) #seconds    , ,      .

            if (t.total <= 0) { //       getTimeRemaining()  total,     ,   ,       .
                clearInterval(timeInterval); //    .
            }

        }

    }


    setClock('.timer', deadline); //       .            .         


    // Modal

    const modalTrigger = document.querySelectorAll('[data-modal]'), //       ()
        modal = document.querySelector('.modal'); //    
    // modalCloseBtn = document.querySelector('[data-close]'); //   

    function openModal() { //    
        modal.classList.add('show');
        modal.classList.remove('hide');
        // modal.classList.toggle('hide');
        document.body.style.overflow = 'hidden'; //  
        clearInterval(modalTimerId);
    }


    modalTrigger.forEach(btn => {
        btn.addEventListener('click', openModal); //    
    }); //          

    function closeModal() { //    
        modal.classList.add('hide');
        modal.classList.remove('show');
        // modal.classList.toggle('hide');
        document.body.style.overflow = ''; //  
    }

    // modalCloseBtn.addEventListener('click', closeModal); //   

    modal.addEventListener('click', (e) => {
        if (e.target === modal || e.target.getAttribute('data-close') == '') {
            closeModal();
        }
    }); //         

    document.addEventListener('keydown', (e) => {
        if (e.key === 'Escape' && !modal.classList.contains('hide')) {
            closeModal();
        }
    }); //         esc

    const modalTimerId = setTimeout(openModal, 50000);

    function showModalByScroll() { //        
        if (window.pageYOffset + this.document.documentElement.clientHeight >= document.documentElement.scrollHeight - 1) {
            openModal();
            window.removeEventListener('scroll', showModalByScroll);
        } //     
    }

    window.addEventListener('scroll', showModalByScroll); //      ,   

    //    
    class MenuCard {
        constructor(src, alt, title, descr, price, parentSelector, ...classes) {
            this.src = src; //  
            this.alt = alt;
            this.title = title;
            this.descr = descr;
            this.price = price;
            this.classes = classes; //             rest
            this.parent = document.querySelector(parentSelector); //  (        )
            this.transfer = 37;
            this.changeToUAH();
        } //    

        changeToUAH() {
            this.price = this.price * this.transfer;
        }  //      

        render() {
            const element = document.createElement('div'); //  ,     
            if (this.classes === 0) { //       (    )
                this.element = 'menu__item';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            element.innerHTML = ` 
                    <img src=${this.src} alt=${this.alt} />
                    <h3 class="menu__item-subtitle">${this.title}</h3>
                    <div class="menu__item-descr">
                        ${this.descr}
                    </div>
                    <div class="menu__item-divider"></div>
                    <div class="menu__item-price">
                        <div class="menu__item-cost">:</div>
                        <div class="menu__item-total"><span>${this.price}</span> /</div>
                    </div>
            `; //           
            this.parent.append(element); //     
        } //     ()
    } //  ()  

    // const div = new MenuCard();
    // div.render();
    new MenuCard(
        "img/tabs/vegy.jpg",
        "vegy",
        ' ""',
        ' "" -      :     .     .          !',
        9,
        '.menu .container',
        'menu__item',
        'big'
    ).render(); //       (         )

    new MenuCard(
        "img/tabs/elite.jpg",
        "elite",
        ' ',
        '         ,     .  , ,  -      !',
        14,
        '.menu .container',
        'menu__item'
    ).render();

    new MenuCard(
        "img/tabs/post.jpg",
        "post",
        ' ""',
        '  -    :     ,   , ,   ,          .',
        21,
        '.menu .container',
        'menu__item'
    ).render();

    // Forms
    //    
    const forms = document.querySelectorAll('form'); //   

    const message = {
        loading: 'img/form/spinner.svg',
        success: ',    ',
        failure: '-   '
    }; //     

    forms.forEach(item => {
        postData(item);
    }); //        postData

    function postData(form) {
        form.addEventListener('submit', (e) => {
            e.preventDefault(); //   

            const statusMessage = document.createElement('img'); //  ()     
            statusMessage.src = message.loading; //   
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;//  
            // form.append(statusMessage); //      
            form.insertAdjacentElement('afterend', statusMessage); //      

            const formData = new FormData(form); //  FormData -                  -

            const object = {}; //         
            formData.forEach(function (value, key) {
                object[key] = value;
            }); //            object

            fetch('server.php', {
                method: "POST",
                headers: {
                    'Content-type': 'application/json'
                },
                body: JSON.stringify(object)
            })
                .then(data => data.text())
                .then(data => {
                    console.log(data); //     
                    showThanksModal(message.success); //      
                    statusMessage.remove(); //      
                }).catch(() => {
                    showThanksModal(message.failure);
                }).finally(() => {
                    form.reset(); //      (input   )
                });
        }); //   
    } //       

    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog'); //   modal__dialog(        )

        prevModalDialog.classList.add('hide'); //        
        openModal(); //    

        const thanksModal = document.createElement('div'); //  
        thanksModal.classList.add('modal__dialog'); //   
        thanksModal.innerHTML = `
            <div class="modal__content"> 
                <div class="modal__close" data-close>&times;</div>
                <div class="modal__title">${message}</div>
            </div>
        `; //  

        document.querySelector('.modal').append(thanksModal); //   
        setTimeout(() => {
            thanksModal.remove(); //    
            prevModalDialog.classList.add('show'); //    ( )
            prevModalDialog.classList.remove('hide'); //    
            closeModal(); //       
        }, 4000); //        
    }

}); //   DOM